@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/montserrat-400.woff2") format("woff2"), url("../fonts/montserrat-400.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/montserrat-500.woff2") format("woff2"), url("../fonts/montserrat-500.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: url("../fonts/montserrat-600.woff2") format("woff2"), url("../fonts/montserrat-600.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/montserrat-700.woff2") format("woff2"), url("../fonts/montserrat-700.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Placeholder", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.list {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.container {
  font-family: "Montserrat", "Arial", sans-serif;
  color: #011c40;
  padding: 110px 30px 120px;
  margin: 0 auto;
  max-width: 1220px;
  width: 100%;
  min-width: 1024px;
}
@media (max-width: 1023px) {
  .container {
    min-width: 768px;
    padding: 90px 45px 95px;
  }
}
@media (max-width: 767px) {
  .container {
    min-width: 320px;
    padding: 57px 15px 85px;
  }
}

.btn {
  display: inline-flex;
  padding: 10px 20px;
  color: #ffffff;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.bg {
  width: 100%;
  height: 100%;
  z-index: 1;
  object-fit: cover;
  position: absolute;
  bottom: 0;
  left: 0;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.main-header {
  z-index: 3;
  min-width: 320px;
  overflow: hidden;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  width: 100%;
  position: absolute;
}
@media (max-width: 1023px) {
  .main-header {
    font-size: 14px;
    line-height: 17px;
  }
}

.main-header__container {
  transition: 0.3s ease;
  padding: 35px 30px 0;
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
}
@media (max-width: 1023px) {
  .main-header__container {
    padding: 30px 45px 25px;
  }
}
@media (max-width: 767px) {
  .main-header__container {
    padding: 20px 15px 20px;
  }
}

.main-header__toggle {
  border: none;
  padding: 0;
  background-color: transparent;
  display: none;
  position: absolute;
  right: 15px;
  top: 24px;
  width: 24px;
  height: 14px;
}
@media (max-width: 767px) {
  .main-header__toggle {
    display: block;
  }
}

.main-header__logo {
  fill: #f9fbfd;
  width: 70px;
  height: 38px;
}
@media (max-width: 1023px) {
  .main-header__logo {
    width: 38px;
    height: 20px;
  }
}
@media (max-width: 767px) {
  .main-header__logo {
    width: 42px;
    height: 22px;
  }
}

.main-header__list {
  display: grid;
  grid-template-columns: repeat(4, max-content);
  grid-column-gap: 33px;
}
@media (max-width: 1023px) {
  .main-header__list {
    grid-column-gap: 24px;
    grid-row-gap: 20px;
  }
}
@media (max-width: 767px) {
  .main-header__list {
    display: none;
  }
}

.main-header__item {
  max-width: 200px;
  display: flex;
  margin: 10px 0;
}
@media (max-width: 1023px) {
  .main-header__item {
    margin: 1px 0;
  }
}

.main-header__link {
  color: #f9fbfd;
}

@media (max-width: 767px) {
  .main-header--opened {
    transition: 300ms;
    height: 100%;
    background: rgba(0, 43, 102, 0.5);
  }
}

@media (max-width: 767px) {
  .main-header--opened .main-header__container {
    transition: 0.3s ease;
    background-color: #f9fbfd;
    margin-left: auto;
    margin-right: 0;
    width: 89%;
    min-width: 285px;
    padding: 20px 15px;
    height: 100%;
    flex-direction: column;
    justify-content: flex-start;
  }
}

@media (max-width: 767px) {
  .main-header--opened .main-header__list {
    display: grid;
    grid-template-columns: max-content;
    grid-row-gap: 26px;
    margin-left: 10px;
  }
}

@media (max-width: 767px) {
  .main-header--opened .main-header__link {
    margin: 0;
    color: #011c40;
    font-size: 18px;
    line-height: 22px;
  }
}

@media (max-width: 767px) {
  .main-header--opened .main-header__logo {
    fill: #011c40;
    margin-bottom: 42px;
  }
}

.main-header--opened .main-header__toggle {
  width: 15px;
}

.main-header--closed .main-header__toggle::after {
  cursor: pointer;
  display: block;
  content: "";
  background-image: url("../img/svg/menu-open.svg");
  width: 24px;
  height: 14px;
}

.main-header--opened .main-header__toggle::after {
  cursor: pointer;
  display: block;
  content: "";
  background-image: url("../img/svg/menu-close.svg");
  width: 14px;
  height: 14px;
  margin-right: 0;
}

.main-header--no-js .main-header__toggle {
  display: none;
}

.main-header--no-js .main-header__list {
  margin-top: 20px;
  display: grid;
  grid-template-columns: repeat(2, max-content);
  grid-row-gap: 5px;
}

.main-banner {
  z-index: 0;
  min-width: 320px;
  overflow: hidden;
  width: 100%;
  min-height: 650px;
  position: relative;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
@media (max-width: 1023px) {
  .main-banner {
    min-height: 638px;
    box-shadow: none;
    text-shadow: none;
  }
}
@media (max-width: 767px) {
  .main-banner {
    min-height: 500px;
  }
}

.main-banner__container {
  color: #f9fbfd;
  z-index: 2;
  position: relative;
  padding: 270px 0 0 125px;
}
@media (max-width: 1023px) {
  .main-banner__container {
    padding-top: 268px;
  }
}
@media (max-width: 767px) {
  .main-banner__container {
    padding-top: 175px;
  }
}

.main-banner__title {
  margin: 0;
  margin-bottom: 13px;
  font-weight: 700;
  font-size: 54px;
  line-height: 66px;
}
@media (max-width: 1023px) {
  .main-banner__title {
    margin-bottom: 12px;
    font-size: 50px;
    line-height: 61px;
  }
}
@media (max-width: 767px) {
  .main-banner__title {
    font-size: 36px;
    line-height: 45px;
  }
}

.main-banner__description {
  margin: 0;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
}
@media (max-width: 1023px) {
  .main-banner__description {
    font-size: 18px;
    line-height: 22px;
  }
}
@media (max-width: 767px) {
  .main-banner__description {
    width: 200px;
    font-size: 16px;
    line-height: 22px;
  }
}

.travel {
  min-width: 320px;
  width: 100%;
  overflow: hidden;
}

.travel__container {
  padding-top: 120px;
  padding-bottom: 145px;
  display: flex;
  justify-content: space-between;
}
@media (max-width: 767px) {
  .travel__container {
    flex-wrap: wrap;
  }
}

.travel__text {
  width: 45%;
  margin-top: 57px;
}
@media (max-width: 1023px) {
  .travel__text {
    margin-top: 0;
    width: 47.5%;
  }
}
@media (max-width: 767px) {
  .travel__text {
    width: 100%;
  }
}

.travel__text h2 {
  margin: 0;
  font-weight: 600;
  font-size: 28px;
  line-height: 42px;
  margin-bottom: 23px;
}
@media (max-width: 1023px) {
  .travel__text h2 {
    margin-bottom: 16px;
    width: 205px;
    font-size: 26px;
    line-height: 36px;
  }
}
@media (max-width: 767px) {
  .travel__text h2 {
    font-size: 24px;
    line-height: 34px;
    margin-bottom: 15px;
  }
}

.travel__text p {
  margin: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
}

.travel__pic-container {
  width: 560px;
  height: 301px;
}
@media (max-width: 1023px) {
  .travel__pic-container {
    margin-top: 10px;
    margin-right: 5px;
    width: 47.5%;
    height: auto;
  }
}
@media (max-width: 767px) {
  .travel__pic-container {
    margin-right: 0;
    margin-top: 43px;
    width: 100%;
  }
