.travel__pic {
  width: 100%;
}

.benefits {
  background-color: #dae7f4;
  position: relative;
  z-index: 0;
  min-width: 320px;
  overflow: hidden;
  width: 100%;
}

.benefits__container {
  position: relative;
  z-index: 2;
}
@media (max-width: 1023px) {
  .benefits__container {
    padding-bottom: 99px;
  }
}
@media (max-width: 767px) {
  .benefits__container {
    padding-bottom: 64px;
  }
}

.benefits__title {
  width: 100%;
  text-align: center;
  margin: 0;
  margin-bottom: 49px;
  font-weight: 600;
  font-size: 28px;
  line-height: 42px;
}
@media (max-width: 1023px) {
  .benefits__title {
    font-size: 26px;
    line-height: 39px;
  }
}
@media (max-width: 767px) {
  .benefits__title {
    font-size: 24px;
    line-height: 36px;
    margin-bottom: 30px;
  }
}

.benefits__list {
  display: grid;
  grid-template-columns: repeat(3, calc((100% - 80px) / 3));
  grid-column-gap: 40px;
  grid-row-gap: 30px;
}
@media (max-width: 1023px) {
  .benefits__list {
    grid-template-columns: repeat(3, calc((100% - 60px) / 3));
    grid-column-gap: 30px;
  }
}
@media (max-width: 767px) {
  .benefits__list {
    grid-template-columns: 100%;
    grid-row-gap: 30px;
