  color: rgba(1, 28, 64, 0.7);
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
}

.booking__checkbox-container {
  position: relative;
  z-index: 3;
}

.booking__checkbox-container input {
  width: 18px;
  height: 18px;
  top: 20px;
  left: 0;
  opacity: 0;
  position: absolute;
}

.booking__checkbox-container label {
  width: 100%;
  display: block;
  margin: 23px 0 30px;
  padding-left: 24px;
  font-weight: 400;
  font-size: 10px;
  line-height: 13px;
}
@media (max-width: 1023px) {
  .booking__checkbox-container label {
    margin: 24px 0 31px;
  }
}
@media (max-width: 767px) {
  .booking__checkbox-container label {
    width: 180px;
    margin: 19px 0 28px;
    line-height: 12px;
    padding-left: 28px;
  }
}

.booking__checkbox-container label::before {
  position: absolute;
  top: -3px;
  left: 0;
  display: block;
  content: "";
  cursor: pointer;
  width: 18px;
  height: 18px;
  background-color: #f9fbfd;
}
@media (max-width: 767px) {
  .booking__checkbox-container label::before {
    top: 0;
    width: 20px;
    height: 20px;
  }
}

.booking__checkbox-container input:checked + label::after {
  position: absolute;
  top: -3px;
  left: 0;
  display: block;
  content: "";
  cursor: pointer;
  width: 18px;
  height: 18px;
  background-image: url("../img/svg/icon-checked.svg");
  background-repeat: no-repeat;
  background-position: 3px 5px;
}
@media (max-width: 767px) {
  .booking__checkbox-container input:checked + label::after {
    top: 0;
    width: 20px;
    height: 20px;
  }
}

